          </li>
        </ol>
      </div>
      <div class="world mgt60">{{content}}</div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      id: this.$route.query.id, //接收的路由参数
      bannerimg: "./static/lib/image/about1.png",
      content:
        "阿斯蒂芬嘎达扶桑法大使馆都十分感动发生过发都是是的分公司电饭锅啥地方噶水电费个是多付过所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所所是大法官过过过过过过过过过过过过是大法官过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过过是大法官过过过过过过过过过过过过过过"
    };
  }
};
</script>
<style scoped>
a {
  cursor: pointer;
}
</style>



