<template>
  <div id="header">
    <div class="bg navbar-fixed-top">
      <div class="container">
        <div class="row clearfix">
          <div class="col-md-12 column">
            <nav class="navbar navbar-default" role="navigation">
              <div class="navbar-header">
                <button
                  type="button"
                  class="navbar-toggle"
                  data-toggle="collapse"
                  data-target="#bs-example-navbar-collapse-1"
                >
                  <span class="sr-only">Toggle navigation</span>
                  <span class="icon-bar"></span>
                  <span class="icon-bar"></span>
                  <span class="icon-bar"></span>
                </button>

                <img class="navbar-brand logo" :src="logoimg" alt="logo">
                <div class="navbar-brand logo_title">
                  <a @click="navchange(0)">天鹅美妆</a>
                  <p>SWAM BEAUTY</p>
                </div>
              </div>
              <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
                <ul id="tabs_nav" class="nav navbar-nav navbar-right">
                  <li @click="navchange(index)" v-for="(item,index) in navbarlist" :key="index">
                    <a :class="{active:navchangeIndex == index}">{{item.title1}}</a>
                  </li>
                </ul>
              </div>
            </nav>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      navchangeIndex: 0,
      logoimg: "./static/lib/image/logo.png",
      navbarlist: [
        { title1: "首页" },
        { title1: "关于我们" },
        { title1: "联系我们" },
        { title1: "行业资讯" }
      ]
    };
  },
  mounted() {
    this.getnavnum();
  },
  methods: {
    getnavnum() {
      this.navchangeIndex = sessionStorage.getItem("navnum");
    },
    setnavnum(index) {
      sessionStorage.setItem("navnum", index);
    },
    navchange(index) {
      this.setnavnum(index);
      if (index == 0) {
        this.$router.push("/");
      } else if (index == 1) {
        this.$router.push("/about");
      } else if (index == 2) {
        this.$router.push("/content");
      } else if (index == 3) {
        this.$router.push("/news");
      }
    }
  }
};
</script>
<style scoped>
a {
  color: #ffffff;
  text-decoration: none;
  cursor: pointer;
}
</style>



