<template>
  <div id="content">
    <Header></Header>
    <div class="float1 float2 baseimglight">
      <img :src="bannerimg" alt="banner" style="width:100%;height:100%">
    </div>
    <div class="container">
      <div class="row clearfix">
        <div class="col-md-12 column">
          <div class="text-center mgt60">
            <h2>CONTENT US</h2>
            <h3>联系我们</h3>
          </div>
        </div>
      </div>
    </div>
    <Contentus></Contentus>
    <div class="container mgtop30">
      <div class="row clearfix">
        <div class="col-md-12 column">
          <form role="form" novalidate="novalidate">
            <div class="yijian">
              <h3 class="text-center mgt20">[ 意见留言 ]</h3>
              <p class="text-center mgt5">请留下您的意见和建议，以便我们为您提供更优质的服务</p>
            </div>
            <div class="row clearfix">
              <div class="col-md-6 column mgt10">
                <div class="form-group">
                  <input
                    type="text"
                    class="form-control baseimglight hoverflow"
                    name="name"
                    id="name"
                    value
                    size="40"
                    aria-invalid="false"
                    placeholder="姓名"
                  >
                </div>
              </div>
              <div class="col-md-6 column mgt10">
                <div class="form-group">
                  <input
                    type="email"
                    class="form-control baseimglight hoverflow"
                    name="email"
                    id="email"
                    value
                    size="40"
                    aria-invalid="false"
                    placeholder="邮箱"
                  >
                </div>
              </div>
            </div>
            <div class="row clearfix">
              <div class="col-md-6 column mgt10">
                <div class="form-group">
                  <input
                    type="text"
                    class="form-control baseimglight hoverflow"
                    name="phone"
                    id="phone"
                    value
                    size="40"
                    aria-invalid="false"
                    placeholder="电话"
                  >
                </div>
              </div>
              <div class="col-md-6 column mgt10">
                <div class="form-group">
                  <input
                    type="text"
                    class="form-control baseimglight hoverflow"
                    name="address"
                    id="address"
                    value
                    size="40"
                    aria-invalid="false"
                    placeholder="地址"
                  >
                </div>
              </div>
            </div>
            <div class="input_textarea mgt10">
              <textarea
                name="message"
                id="message"
                class="baseimglight hoverflow"
                cols="40"
                value
                rows="10"
                aria-invalid="false"
                placeholder="您想对我们说的话..."
              ></textarea>
            </div>
            <button class="form_btn mgt60 baseimglight hoverflow" type="submit">提交您的信息</button>
          </form>
        </div>
      </div>
    </div>
    <div style="background:#e4e4e4;">
      <div class="container">
        <div class="row clearfix">
          <div class="col-md-12 column">
            <div class="mgt60 float1 float2 baseimglight hoverflow">
              <img :src="serverimg" alt="图片" style="width:100%;height:100%">
            </div>
            <div class="row clearfix mgt60" v-for="(item,index) in serverlist" :key="index">
              <div class="col-md-3 column">
                <div class="text-center baseimglight">
                  <img :src="item.img" alt="index">
                </div>
              </div>
              <div class="col-md-9 column">
                <p
                  class="text-left"
                  v-for="(item,index) in item.contentlist"
                  :key="index"
                >{{item.name}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import Header from "../public/Header";
import Contentus from "../public/Contentus";
import Footer from "../public/Footer";

export default {
  name: "Content",
  components: {
    Header,
    Contentus,
    Footer
  },
  data() {
    return {
      bannerimg: "./static/lib/image/about1.png",
      serverimg: "./static/lib/image/about2.png",
      serverlist: [
        {
          img: "./static/lib/image/content6.png",
          contentlist: [
            { name: "商务合作：" },
            { name: "电话：010-57532700" },
            { name: "Email：il：bd@harmay.com" }
          ]
        },
        {
          img: "./static/lib/image/content7.png",
          contentlist: [
            { name: "售后服务：" },
            { name: "服务热线：400-890-6878" },
            { name: "服务时间：09:00-24:00" },
            { name: "Email：il：customer@harm" }
          ]
        }
      ]
    };
  },
  mounted() {}
};
</script>
<style scoped>
</style>


