<template>
  <div id="contentus">
    <div class="container mgt30">
      <div class="row clearfix">
        <div class="col-md-12 column">
          <div class="row clearfix">
            <div class="col-md-4 column" v-for="(item,index) in contentlist" :key="index">
              <div class="public_box">
                <div class="header_img baseimglight hoverflow">
                  <img :src="item.img1" alt="index" width="100%;height:100%;">
                </div>
                <p class="text-center">{{item.title1}}</p>
                <p class="text-center body_img" v-if="index==1">{{item.img2}}</p>
                <div class="body_img baseimglight hoverflow" v-if="index!=1">
                  <img :src="item.img2" alt="index" width="100%;height:100%;">
                </div>

                <div class="body_btn text-center baseimglight hoverflow" v-if="index==1">
                  <a :href="phone">{{item.title2}}</a>
                </div>
                <div
                  class="body_btn text-center baseimglight hoverflow"
                  v-if="index!=1"
                >{{item.title2}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      phone: "tel:13537280900",
      contentlist: [
        {
          title1: "天鹅小程序",
          title2: "欢迎咨询",
          img1: "./static/lib/image/content2.png",
          img2: "./static/lib/image/content1.png"
        },
        {
          title1: "联系电话",
          title2: "联系我们",
          img1: "./static/lib/image/content4.png",
          img2: "13537280900 0759-5908271"
        },
        {
          title1: "天鹅公众号",
          title2: "欢迎咨询",
          img1: "./static/lib/image/content5.png",
          img2: "./static/lib/image/content3.png"
        }
      ]
    };
  }
};
</script>



