import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index/Index'
import About from '@/components/about/About'
import Content from '@/components/content/Content'
import News from '@/components/news/News'
import Industry from '@/components/news/Industry'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'Index',
      component: Index,
      meta: {
        title: '天鹅美妆 | 首页' // 标题设置
      }
    }, {
      path: '/about',
      name: 'About',
      component: About,
      meta: {
        title: '天鹅美妆 | 关于我们' // 标题设置
      }
    }, {
      path: '/content',
      name: 'Content',
      component: Content,
      meta: {
        title: '天鹅美妆 | 联系我们' // 标题设置
      }
    }, {
      path: '/news',
      name: 'News',
      component: News,
      meta: {
        title: '天鹅美妆 | 行业资讯' // 标题设置
      }
    },
    {
      path: '/industry',
      name: 'Industry',
      component: Industry,
      meta: {
        title: '天鹅美妆 | 资讯详情' // 标题设置
      }
    },
  ]
})



